import { FETCH_ALL, FETCH_POST, CREATE, UPDATE, COMMENT, DELETE, FETCH_BY_SEARCH, START_LOADING, END_LOADING } from '../constants/actionTypes';

export default (state = { isLoading: true, posts: [] }, action) => {
  switch (action.type) {

    case START_LOADING:
      return { ...state, isLoading: true};

    case END_LOADING:
      return { ...state, isLoading: false};

    case FETCH_ALL:
      return { ...state, posts: action.payload.data, currentPage: action.payload.currentPage, numberOfPages: action.payload.numberOfPages };

    case FETCH_BY_SEARCH:
      return { ...state, posts: action.payload.data };//.data
    
    case FETCH_POST:
      return { ...state, post: action.payload };

    case CREATE:
      return { ...state, posts: [...state.posts, action.payload] }; //where new posts are saved 

    case UPDATE:
      return { ...state, posts: state.posts.map((post) => (post._id === action.payload._id ? action.payload : post)) }; //action.payload is the updatedPost

    case COMMENT:
      return  { ...state, posts: state.posts.map((post) => {
        //Checks commented post to all posts
        if(post._id === action.payload._id) {
          return action.payload;
        }
        return post; 
      })}

    case DELETE:
      return { ...state, posts: state.posts.filter((post) => post._id !== action.payload._id) };
      
    default:
      return state;
  }
}

